@mixin input {
   font-family: inherit;
   font-size: inherit;
   color: inherit;
   background-color: #fff;
   border: none;
   padding: 0.5rem 1.2rem 0.5rem 2.5rem;
   border-radius: 10px;
   transition: all 0.2s;
}
@keyframes slideInDown {
   0% {
      -webkit-transform: translateY(-15%);
      transform: translateY(-15%);
      visibility: visible;
      -moz-transform: translateY(-15%);
      -ms-transform: translateY(-15%);
      -o-transform: translateY(-15%);
   }

   100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
   }
}

.loading {
   &-spinner {
      position: absolute;
      display: grid;
      place-self: center;

      width: 5.5rem;
      height: 5.5rem;
      border-radius: 50%;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      -ms-border-radius: 50%;
      -o-border-radius: 50%;
      border: 5px solid rgba(228, 224, 217, 0.856);
      border-left-color: var(--color-blue);
      background-color: transparent;
      animation: rotate-loader 1.5s linear infinite;
      -webkit-animation: rotate-loader 1.5s linear infinite;
      animation-delay: 0.2s;
   }
   &-button {
      width: 2rem;
      height: 2rem;
      margin: 0.1rem 2rem;
      border-radius: 50%;
      -webkit-border-radius: 50%;
      border: 2px solid rgba(228, 224, 217, 0.856);
      border-left-color: var(--color-blue);
      background-color: transparent;
      animation: rotate-loader 1.5s linear infinite;
      -webkit-animation: rotate-loader 1.5s linear infinite;
      animation-delay: 0.2s;
   }
}

@keyframes rotate-loader {
   from {
      transform: rotate(0);
      -webkit-transform: rotate(0);
      -moz-transform: rotate(0);
      -ms-transform: rotate(0);
      -o-transform: rotate(0);
   }

   to {
      transform: rotate(360deg);
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      -o-transform: rotate(360deg);
   }
}
